@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap);
/* custom colors  */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

:root {
  --bgcolor: #f1f5fd;
  --primary-color: #6f5de7;
  --text-color: #828286;
  --main-text-color: #333333;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333333;
}

/* --------------------------------- navbar css style starts 
                        -------------------------------------------------------*/

.navbar-bg {
  background-color: #f1f5fd;
  background-color: var(--bgcolor);
  padding: 2rem 0;
  z-index: 99;
  position: relative;
}

.navbar-brand {
  font-size: 2.5rem;
}

.navbar li {
  padding: 0 2rem;
}

.navbar li a {
  font-size: 1.7rem;
  font-weight: lighter;
  position: relative;
}

.navbar li a:hover {
  color: #6f5de7 !important;
  color: var(--primary-color) !important;
  text-shadow: 0 0.2rem 0.3rem rgba(183, 171, 255, 0.5);
}

.btn-style {
  width: 12rem;
  height: 4rem;
  border-radius: 2rem;
  color: #fff;
  background-color: #6f5de7;
  background-color: var(--primary-color);
  text-transform: capitalize;
  font-size: 1.6rem;
}

.btn-style:first-child {
  margin-right: 2rem;
}

.btn-style:hover {
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: #6f5de7;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}

.btn-style-border {
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: #6f5de7;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}

.btn-style-border:hover {
  color: #fff;
  background-color: #6f5de7;
  background-color: var(--primary-color);
  text-transform: capitalize;
}
/* ---------------------------------- Hero Section---------------------------------------- */
header {
  height: 100vh;
  width: 100%;
  background-color: #f1f5fd;
  background-color: var(--bgcolor);
}
.main-hero-container {
  height: 80vh;
  display: flex;
}
.main-hero-container h1 {
  text-transform: capitalize;
  font-weight: bolder;
}
.main-hero-para {
  font-size: 1.5rem;
  color: #828286;
  color: var(--text-color);
  margin: 3rem 0 5rem 0;
  font-weight: bolder;
  width: 75%;
}
.main-hero-container h3 {
  text-transform: capitalize;
  font-weight: lighter;
}
.main-herosection-images {
  position: relative;
}
.main-herosection-images img {
  width: 60%;
  height: 75%;
  margin-top: 10rem;
  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.25);
  border-radius: 1.5rem;
  position: absolute;
}
.main-herosection-images .main-hero-img2 {
  position: absolute;
  width: 40%;
  height: 30%;
  left: 55%;
  top: -4%;
  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.25);
}
.input-group {
  position: relative !important;
}
.form-control-text {
  position: absolute;
  height: 5rem;
  border: none;
  outline-style: none;
  padding-left: 1.5rem !important;
  font-size: 1.4rem;
  font-weight: lighter;
}
.input-group-button {
  position: absolute;
  right: 26%;
  top: 0.5rem;
  color: #fff;
  background-color: #6f5de7;
  background-color: var(--primary-color);
  height: 40px;
  width: 15%;
  border-radius: 2rem !important;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
}
/* --------------------------------- Work Section  
                        -------------------------------------------------------*/

.work-container {
  width: 80vw;
  padding: 5rem;
  box-shadow: 0 0.4rem 1.2rem rgba(0, 0, 0, 0.1);
  margin-top: -10rem;
  background-color: #fff;
  border-radius: 0.5rem;
}
.work-container .main-heading {
  font-size: 3rem;
  font-weight: bolder;
  margin-bottom: 4rem;
  word-spacing: 1rem;
}
.work-container-subdiv {
  padding: 0 6rem;
}
.work-container .sub-heading {
  font-size: 1.7rem;
  font-weight: lighter;
}
.fontawesome-style {
  font-size: 2.3rem;
  color: #6f5de7;
  color: var(--primary-color);
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background-color: #f1f5fd;
  background-color: var(--bgcolor);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
/* --------------------------------- About Us  
                        -------------------------------------------------------*/
.common-section {
  margin: 5rem 0;
}
.our-services img {
  width: 40%;
  height: 75%;
  margin-top: 100px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.8rem 0.8rem;
  border-radius: 1.5rem;
}
.our-services .mini-title {
  text-transform: uppercase;
  margin: 3rem 0;
  font-weight: normal;
  text-align: left;
  font-size: 1.5rem;
}
.our-services .main-heading {
  font-weight: bolder;
}
.our-services .our-services-info {
  margin-top: 4rem;
}
.our-services .our-services-number {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border-radius: 50%;
  border: 0.1rem solid #6f5de7;
  border: 0.1rem solid var(--primary-color);
  font-size: 1.5rem;
  color: #6f5de7;
  color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.our-services .our-services-data {
  padding-left: 2rem;
}
.our-services .our-services-data p {
  margin: 1rem 0;
}
/* 2nd section */
.our-services-rightside-content {
  padding-left: 80px;
}

/* --------------------------------- Services Section starts  
                        -------------------------------------------------------*/

.service-main-container {
  margin-top: 5rem;
  padding: 5rem 0;
}

.service-main-container .main-heading {
  margin: 3rem 0;
}

.work-container-subdiv {
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.16);
}

.work-container-subdiv:hover {
  background-color: #f1f5fd;
  background-color: var(--bgcolor);
  cursor: pointer;
}

.work-container-subdiv:hover > .fontawesome-style {
  background-color: #fff;
}

.work-container-subdiv .main-hero-para {
  width: 100%;
}

.service-main-container .work-container-subdiv .fontawesome-style {
  text-align: left;
  margin: 2rem 0;
}

@media (min-width: 1400px) {
  .service-container .col-xxl-4 {
    flex: 0 0 auto;
    width: 30% !important;
    margin: auto;
  }
}

@media (min-width: 992px) {
  .service-container .col-lg-4 {
    flex: 0 0 auto;
    width: 30% !important;
    margin: 2.5rem auto;
  }
}
/* --------------------------------- Contact Section starts  
                        -------------------------------------------------------*/
.contact-section {
  margin: 15rem 0;
}

.contact-leftside img {
  min-width: 80%;
  max-height: 30rem;
  height: auto;
}
.contact-input-field {
  margin-bottom: 4rem;
}
.contact-rightside form .form-control {
  border: 1px solid #ced4da;
  height: 5rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  padding-left: 1rem;
  font-weight: lighter;
}
.contact-rightside form .form-check-input {
  width: 2rem;
  height: 2rem;
}
.form-checkbox-style {
  margin: 4rem 0;
}
.contact-rightside form .main-hero-para {
  font-size: 1.5rem;
  color: #828286;
  color: var(--text-color);
  margin: 0;
  font-weight: lighter;
  width: 75%;
  padding: 0 2rem;
}
/* --------------------------------- Footer 
                        -------------------------------------------------------*/

footer {
  padding: 5rem 0;
  background-color: #182334;
  color: #fff;
}
footer h2 {
  color: #fff;
}
footer li {
  list-style: none;
  font-size: 1.5rem;
  font-weight: lighter;
  color: #fff;
  padding: 1rem 0;
}
footer li:hover {
  color: #6f5de7;
  color: var(--primary-color);
}
footer .fontawesome-style {
  font-size: 2.3rem;
  color: #6f5de7;
  color: var(--primary-color);
  background-color: transparent;
  width: 0rem;
  height: 0rem;
  margin: 2rem 0;
  cursor: pointer;
}
footer .fontawesome-style:hover {
  transition: all 1s;
  animation: rotatefont 1s linear infinite alternate-reverse;
  color: #fff;
}
@-webkit-keyframes rotatefont {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
@keyframes rotatefont {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
footer .main-hero-para {
  margin: 0;
}
/* --------------------------------- responsive media queries  
                        -------------------------------------------------------*/
@media (min-width: 1500px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1500px !important;
  }
}

@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1400px;
  }
}

